@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;400;600&display=swap);
:root {
  --bg: #ebebeb;
  --color: #1c1c20;
}
header {
  height: 60px;
  background-color: #ebebeb;
  background-color: var(--bg);
}

.nav-menu {
  background-color: #ebebeb;
  background-color: var(--bg);
  box-shadow: 1px 0px 10px #000;
  width: 250px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 10;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  /** li */
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}
.nav-text a {
  text-decoration: none;
  color: #1c1c20;
  color: var(--color);
  font-size: 22px;
  width: 90%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 16px;
}
.nav-text a:hover {
  color: #fff;
  background-color: #1c1c20;
  background-color: var(--color);
}

.nav-menu-items {
  width: 100%;
  margin-top: 50px;
}
.endline {
  position: absolute;
  bottom: 0%;
  width: 100%;
  height: 10%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #000;
  border-top: 1px solid #1c1c20;
  border-top: 1px solid var(--color);
}
.sci {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.sci li {
  list-style: none;
  padding: 5px 10px;
}
.sci li a {
  text-decoration: none;
  color: #fff;
  font-size: 12px;
}
.icon {
  position: absolute;
  width: 40px;
  height: 40px;
  right: 40px;
  top: 20px;
  cursor: pointer;
}

.icon .hamburger {
  width: 25px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition: 0.5s;
  background: #1c1c20;
  background: var(--color);
}

.icon .hamburger::before,
.icon .hamburger::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 2px;
  background: #1c1c20;
  background: var(--color);
  transition: 0.2s;
}

.icon .hamburger::before {
  top: -8px;
}
.icon .hamburger::after {
  top: 8px;
}

.icon.active .hamburger::before {
  top: 0;
}
.icon.active .hamburger::after {
  top: 0;
}

