.singlePostContainer {
  margin-top: 50px;
  justify-content: flex-start;
}
.singlePost {
  width: 80%;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10px;
  padding-bottom: 60px;
  margin-bottom: 20px;
}

.singlePost .imgBx {
  width: 100%;
  height: 300px;
  border-radius: 5px;
}
.singlePost .imgBx img {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  object-fit: cover;
  border-radius: 5px;
}
.head {
  border-bottom: 1px solid #888;
  padding: 20px 10px;

  margin-bottom: 20px;
}
.head .title {
  font-size: 22px;
  text-align: left;
  width: 100%;
  margin-bottom: 15px;
}
.head .author {
  color: rgb(116, 116, 116);
  font-size: 14px;
  width: 100%;
  text-align: right;
}

.blog {
  padding: 0 30px;
  margin-bottom: 30px;
}
.blog p {
  font-size: 16px;
  line-height: 28px;
  letter-spacing: 1px;
  color: #666;
}
.blog p::first-letter {
  margin-left: 20px;
  font-size: 30px;
  font-weight: 600;
}

.hr-tag {
  border: 0;
  clear: both;
  display: block;
  width: 96%;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
}

.empty {
  border: none;
  outline: none;
  width: 100%;
  height: 60px;
}
